import React, { useState } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
} from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControlLabel,
  FormLabel,
  FormGroup,
  Radio,
  RadioGroup,
  Slider,
  Switch,
  TextField,
} from "@mui/material";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Autocomplete from "@mui/material/Autocomplete";
import Stack from "@mui/material/Stack";
import GeoprahicPreference from "./GeoprahicPreference";

import '../styles/body/UserInput.css'

function UserInput() {
  const [age, setAge] = React.useState("");
  const [satScore, setSatScore] = useState(false);

  const handleChange = (event) => {
    setAge(event.target.value);
  };

  const handleResidencyStatus = (event) => {
    if (event.target.value === 2) {
      document.getElementById("citenship_status").style.display = "block";
    } else {
      document.getElementById("citenship_status").style.display = "none";
    }
  };

  const college = [
    { title: "4 Public" },
    { title: "4 private" },
    { title: "community college only" },
  ];

  const fields = [
    { title: "Engineering Mechanics" },
    { title: "Electro Mechanical Engineering" },
    { title: "Liberal And Arts" },
  ];

  const displayItem = () => {
    var sat = document.getElementById("psat_score_enable");

    console.log("asfd");
  };

  return (
    <div class='UserInputBody'>
      <center><h1 style={{padding: '20px'}}>About You</h1></center>
      <MDBContainer fluid>
      <MDBRow className="justify-content-center align-items-center m-2">
        <MDBCard>
          <MDBCardBody className="px-4">
            <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
              Demographic Information
            </h3>
            <MDBRow>
              <MDBCol md="4">
                <TextField
                  id="student-first-name"
                  label="Student First Name"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="student-middle-name"
                  label="Student Middle Name"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="student-last-name"
                  label="Student Last Name"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="residential-status-select-label">
                    Residential Status :{" "}
                  </InputLabel>
                  <Select
                    labelId="residential-status-select-label"
                    id="residential-status-select"
                    label="Residential Status"
                    onChange={handleResidencyStatus}
                  >
                    <MenuItem value={1}>US Citizen or Permanent Resident</MenuItem>
                    <MenuItem value={2}>International</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="8" style={{ display: "none" }} id="citenship_status">
                <TextField
                  id="residential-status-non-us-text-field"
                  label="Citizenship Status"
                  variant="standard"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br/>
            <MDBRow>
              <MDBCol md="12">
                <TextField
                  id="student-address-line1"
                  label="Street Address"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br/>
            <MDBRow>
              <MDBCol md="4">
                <TextField
                  id="student-address-line1"
                  label="City"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="student-address-line1"
                  label="State"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="student-address-line1"
                  label="ZIP Code"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
              <MDBCol md="6">
                <FormControl>
                  <FormLabel id="demo-row-radio-buttons-group-label">
                    Gender
                  </FormLabel>
                  <RadioGroup
                    row
                    aria-labelledby="demo-row-radio-buttons-group-label"
                    name="row-radio-buttons-group"
                  >
                    <FormControlLabel
                      value="female"
                      control={<Radio />}
                      label="Female"
                    />
                    <FormControlLabel
                      value="male"
                      control={<Radio />}
                      label="Male"
                    />
                    <FormControlLabel
                      value="Not Binary"
                      control={<Radio />}
                      label="Not Binary"
                    />
                    <FormControlLabel
                      value="Prefer Not to say"
                      control={<Radio />}
                      label="Prefer Not to Say"
                    />
                  </RadioGroup>
                </FormControl>
              </MDBCol>
              <MDBCol md="6">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Student's Ethinic Origin
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="StudentsEthinicOrigin"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Caucassian</MenuItem>
                    <MenuItem value={20}>African American</MenuItem>
                    <MenuItem value={30}>Hispanic</MenuItem>
                    <MenuItem value={30}>Two or more races</MenuItem>
                    <MenuItem value={30}>Unknown</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
        <h1> </h1>
        <MDBCard>
          <MDBCardBody className="px-4">
            <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
              Student Academics
            </h3>
            <MDBRow>
            <MDBCol md="8">
                <FormControl fullWidth>
                  <InputLabel id="high-school-graduation-completion-year-select-label">
                  Name of High School
                  </InputLabel>
                  <Select
                    labelId="high-school-graduation-completion-year-select-label"
                    id="high-school-graduation-completion-year-select"
                    label="high school graduation completion year"
                  >
                    <MenuItem value={10}>school 1</MenuItem>
                    <MenuItem value={20}>school 2 </MenuItem>
                    <MenuItem value={30}>school 3</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="high-school-graduation-completion-year-select-label">
                  High School Graduation (12th grade)/completion year
                  </InputLabel>
                  <Select
                    labelId="high-school-graduation-completion-year-select-label"
                    id="high-school-graduation-completion-year-select"
                    label="high school graduation completion year"
                  >
                    <MenuItem value={10}>2022</MenuItem>
                    <MenuItem value={20}>2023</MenuItem>
                    <MenuItem value={30}>2024</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
             
            </MDBRow>
            <br />
            <MDBRow>
            <MDBCol md="4">
                <TextField
                  id="standard-basic"
                  type="number"
                  label="Looking to Enroll in"
                  variant="standard"
                  fullWidth
                />
              </MDBCol>
           
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    ENROLLED AS
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="ENROLLED AS"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>FIRST YEAR</MenuItem>
                    <MenuItem value={20}>TRANSFER</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Transfer Year
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="Transfer year"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Sophmore</MenuItem>
                    <MenuItem value={20}>Junior</MenuItem>
                    <MenuItem value={30}>Senior</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
            <br />

            <MDBRow>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Student Class Rank (percentile)
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Top 10%</MenuItem>
                    <MenuItem value={20}>Top Quarter</MenuItem>
                    <MenuItem value={30}>Top half</MenuItem>
                    <MenuItem value={40}>Bottom Half</MenuItem>
                    <MenuItem value={50}>Bottom Quarter</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="standard-basic"
                  type="number"
                  label="Student GPA"
                  variant="standard"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
            <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Transfer Year
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="Transfer year"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Certificate</MenuItem>
                    <MenuItem value={20}>Associate Degree</MenuItem>
                    <MenuItem value={30}>Bachelor's Degree</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <p>
                    International Baccalauerate (IB)
                    <Switch labelId="demo-simple-select-label" />
                  </p>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="standard-basic"
                  type="number"
                  label="Predicted IB Score (Out of 45)"
                  variant="standard"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
              <MDBCol md="2">
                <FormControlLabel
                  control={<Checkbox />}
                  label="PSAT SCORE "
                  labelPlacement="end"
                  id="psat_score_enable"
                />
              </MDBCol>
              <MDBCol md="4">
                <p>
                  PSAT Composite :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="2">
                <FormControlLabel
                  control={<Checkbox />}
                  label="SAT SCORE "
                  labelPlacement="end"
                  id="sat_score_enable"
                />
              </MDBCol>
              <MDBCol md="4">
                <p>
                  SAT Composite :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="3">
                <p>
                  PSAT VERBAL :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  PSAT MATHS :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  SAT VERBAL :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  SAT MATHS :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
            </MDBRow>
            <MDBRow>
            <MDBCol md="2">
                <FormControlLabel
                  control={<Checkbox />}
                  label="ACT SCORE "
                  labelPlacement="end"
                  id="psat_score_enable"
                />
              </MDBCol>
              <MDBCol md="4">
                <p>
                  Act Composite :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBRow>
              <MDBCol md="3">
                <p>
                  ACT VERBAL :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  ACT MATHS :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  ACT ENGLISH :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  ACT SCIENCE :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
            </MDBRow>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
        <h1> </h1>
        <MDBCard>
          <MDBCardBody className="px-4">
            <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
             Preferences and Motivations
            </h3>
            <MDBRow>
              <MDBCol md="4">
                <Stack spacing={3}>
                  <Autocomplete
                    multiple
                    id="tags-outlined"
                    options={college}
                    getOptionLabel={(option) => option.title}
                    filterSelectedOptions
                    renderInput={(params) => (
                      <TextField
                        {...params}
                        label="College Preference"
                        placeholder="College Preference"
                      />
                    )}
                  />
                </Stack>
              </MDBCol>
              <MDBCol md="4">
                <Stack spacing={3}>
                  <Autocomplete
                    multiple
                    id="tags-outlined"
                    options={fields}
                    getOptionLabel={(option) => option.title}
                    filterSelectedOptions
                    renderInput={(params) => (
                      <TextField
                        {...params}
                        label="Planned Field of Study"
                        placeholder="Planned Field of Study"
                      />
                    )}
                  />
                </Stack>
              </MDBCol>
              <MDBCol md="4">
                <GeoprahicPreference />
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
              <MDBCol md="6">
                <FormControl component="fieldset">
                  <FormLabel component="legend">School Size</FormLabel>
                  <FormGroup aria-label="position" row>
                    <FormControlLabel
                      value="Affinity - perceived level of fit with the school"
                      control={<Checkbox />}
                      label="SMALL"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Affordability - whether I/we can afford it"
                      control={<Checkbox />}
                      label="MEDIUM"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Admissibility and Academics - reputation and my chances of getting in"
                      control={<Checkbox />}
                      label="LARGE"
                      labelPlacement="end"
                    />
                  </FormGroup>
                </FormControl>
              </MDBCol>
              <MDBCol md="6">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Religious Affliations
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                    fullWidth
                  >
                    <MenuItem value={10}>Very Important</MenuItem>
                    <MenuItem value={10}>Somewhat Important</MenuItem>
                    <MenuItem value={10}>Not At all Important</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="6">
                <FormControl component="fieldset">
                  <FormLabel component="legend">Urbanicity</FormLabel>
                  <FormGroup aria-label="position" row>
                    <FormControlLabel
                      value="Affinity - perceived level of fit with the school"
                      control={<Checkbox />}
                      label="City"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Affordability - whether I/we can afford it"
                      control={<Checkbox />}
                      label="SubUrban"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Admissibility and Academics - reputation and my chances of getting in"
                      control={<Checkbox />}
                      label="Town"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Admissibility and Academics - reputation and my chances of getting in"
                      control={<Checkbox />}
                      label="Rural"
                      labelPlacement="end"
                    />
                  </FormGroup>
                </FormControl>
              </MDBCol>
              <MDBCol md="6">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Specialized Mission
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Very Important</MenuItem>
                    <MenuItem value={10}>Somewhat Important</MenuItem>
                    <MenuItem value={10}>Not At all Important</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
              <MDBCol md="6">
                <FormControl component="fieldset">
                  <FormLabel component="legend">
                  What are the main reasons to attend an undergraduate college?
                  </FormLabel>
                  <FormGroup aria-label="position">
                    <FormControlLabel
                      value="Good job / maximize income upon graduation"
                      control={<Checkbox />}
                      label="Good job / maximize income upon graduation"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Kinship / Making friends"
                      control={<Checkbox />}
                      label="Kinship / Making friends"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Obtain a credential to pursue graduate education"
                      control={<Checkbox />}
                      label="Obtain a credential to pursue graduate education"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Other"
                      control={<Checkbox />}
                      label="Other"
                      labelPlacement="end"
                    />
                     <MDBCol md="8" id="other">
                  <TextField
                    id="residential-status-non-us-text-field"
                    label="Reason?"
                    variant="standard"
                    fullWidth
                  />
                </MDBCol>
                  </FormGroup>
                </FormControl>
              </MDBCol>
              <MDBCol md="6">
                <FormControl component="fieldset">
                  <FormLabel component="legend">
                  What are your primary considerations when selecting a college?
                  </FormLabel>
                  <FormGroup aria-label="position">
                    <FormControlLabel
                      value="Affinity - reputation/ranking, campus safety, transporation access, weather, and other related factors"
                      control={<Checkbox />}
                      label="Affinity - reputation/ranking, campus safety, transporation access, weather, and other related factors"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Affordability - whether I/we can afford it"
                      control={<Checkbox />}
                      label="Affordability - whether I/we can afford it"
                      labelPlacement="end"
                    />
                    <FormControlLabel
                      value="Admissibility - chances of getting in"
                      control={<Checkbox />}
                      label="Admissibility -  chances of getting in"
                      labelPlacement="end"
                    />
                  </FormGroup>
                </FormControl>
              </MDBCol>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
        <h1> </h1>
        <MDBCard>
          <MDBCardBody className="px-4">
            <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
              Financial Information
            </h3>
            <MDBRow>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <p>
                    Are you Planning to Apply for Financial Aid ?
                    <Switch labelId="demo-simple-select-label" defaultChecked />
                  </p>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                  How important is it to attend a college that offers merit scholarships?
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Very Important</MenuItem>
                    <MenuItem value={10}>Somewhat Important</MenuItem>
                    <MenuItem value={10}>Not At all Important</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Who Will Pay For College ?
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Student Only</MenuItem>
                    <MenuItem value={10}>Student And Family</MenuItem>
                    <MenuItem value={10}>Family Only</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
            <br/>
            <MDBRow>
            <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                  Student Expected Annual Income
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>$0-$10,000</MenuItem>
                    <MenuItem value={10}>$10,001-$20,000</MenuItem>
                    <MenuItem value={10}>$21,000-30,000</MenuItem>
                    <MenuItem value={10}>$31,000-40,000</MenuItem>
                    <MenuItem value={10}>$41,000-50000</MenuItem>
                    <MenuItem value={10}> $50,000+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                  Gross family annual household income from all sources
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>0 - 30000 </MenuItem>
                    <MenuItem value={10}>300001 - 48000</MenuItem>
                    <MenuItem value={10}>48001 - 75000</MenuItem>
                    <MenuItem value={10}>75001 - 110000</MenuItem>
                    <MenuItem value={10}>11000+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                  Total Net Worth including retirement accounts and primary residence
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>0 - 10000</MenuItem>
                    <MenuItem value={10}>10001 - 500000</MenuItem>
                    <MenuItem value={10}>501000 - 1M</MenuItem>
                    <MenuItem value={10}>1M - 2M</MenuItem>
                    <MenuItem value={10}>2M+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
            <br/>
            <MDBRow>
            <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                  Amount saved for college (including 529 accounts for US residents)
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>0-$10,000 </MenuItem>
                    <MenuItem value={10}> $10,001-$25,000</MenuItem>
                    <MenuItem value={10}>$25,001-$50,000</MenuItem>
                    <MenuItem value={10}>$50,0001-$100,000</MenuItem>
                    <MenuItem value={10}> $100,001 - $200,000</MenuItem>
                    <MenuItem value={10}>$200,001-$400,000</MenuItem>
                    <MenuItem value={10}> $400,000+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                  Planned monthly contributions towards college fund
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>$0-$100 </MenuItem>
                    <MenuItem value={10}>$101-$250</MenuItem>
                    <MenuItem value={10}>$251-$500</MenuItem>
                    <MenuItem value={10}>$501-$1,000</MenuItem>
                    <MenuItem value={10}>$1,001-$2,000</MenuItem>
                    <MenuItem value={10}>$2,001-$4,000</MenuItem>
                    <MenuItem value={10}>$4,000+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
      </MDBRow>
    </MDBContainer>
    </div>
  );
}

export default UserInput;
